<template>
  <div class="padding-x">
    <base-modal :show="showModal" @close="toggleModal">
      <user-modal :user="currentAdmin">
        <div class="actions" v-if="currentAdmin">
          <base-button-secondary
            class="button-outline"
            v-if="currentAdmin.isAdmin"
            >Admin</base-button-secondary
          >
          <base-button-secondary
            v-if="!adminIsLoggedIn(currentAdmin._id)"
            class="button"
            @click="toggleReplySender"
            >Send a message</base-button-secondary
          >

          <base-button-secondary
            v-if="adminIsLoggedIn(currentAdmin._id)"
            class="button"
            @click="gotoProfilePage"
            >View Profile</base-button-secondary
          >
        </div>

        <!-- Send a message -->
        <div class="reply-wrapper" v-if="showReplySender">
          <textarea
            rows="5"
            placeholder="Write your message here..."
            v-model="reply"
          ></textarea>
          <p class="error" v-if="!formIsValid">
            Please fill-out the message first!
          </p>
          <base-button-secondary class="button" @click="sendMessage"
            >Send</base-button-secondary
          >
        </div>
      </user-modal>
    </base-modal>
    <div class="title">
      <h2>Admins</h2>
    </div>
    <hr />

    <!-- The list -->
    <div v-if="admins && admins.length != 0" class="">
      <base-course-title
        v-for="(admin, index) in admins"
        :key="index"
        :title="`${admin.firstname} ${admin.lastname}`"
      >
        <template v-slot:default>{{ index + 1 }}. </template>
        <template v-slot:end>
          <a class="edit" @click="toggleModal(admin)">View</a>
        </template>
      </base-course-title>
    </div>
  </div>
</template>

<script>
import UserModal from './UserModal.vue';
export default {
  components: {
    UserModal,
  },
  data() {
    return {
      showModal: false,
      currentAdmin: null,
      reply: '',
      formIsValid: true,
      showReplySender: false,
    };
  },
  computed: {
    admins() {
      return this.$store.getters['admins/admins'];
    },
    loggedInUserEmail() {
      return this.$store.getters.email;
    },
  },
  methods: {
    adminIsLoggedIn(adminId) {
      const userId = this.$store.getters.userId;
      return userId === adminId ? true : false;
    },
    toggleReplySender() {
      this.showReplySender = !this.showReplySender;
    },
    toggleModal(admin) {
      this.showModal = !this.showModal;

      this.currentAdmin = admin;
    },
    async sendMessage() {
      if (!this.reply) {
        this.formIsValid = false;
        return false;
      }

      try {
        const data = {
          email: this.loggedInUserEmail,
          message: this.reply,
          userId: this.currentAdmin._id,
          senderId: this.$store.getters.userId,
        };
        await this.$store.dispatch('requests/contactCoach', data);
      } catch (e) {
        console.log(e);
      }

      this.showReplySender = false;
    },
    gotoProfilePage() {
      this.$router.push('/profile');
    }
  }
};
</script>

